.concept .container {
  padding: 182px 103px 90px;
  background: url("../img/concept-bg-1.png") 0 -284% no-repeat,
    url("../img/concept-bg-2.png") 100% 56.4% no-repeat;

  @include vp-1279 {
    background: url("../img/concept-1-mob.png") -90% -4% no-repeat,
      url("../img/concept-2-mob.png") 52% 29% no-repeat,
      url("../img/concept-3-mob.png") 53% 78% no-repeat;
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    width: 100%;
  }
}

.concept__first-screen {
  display: flex;
  margin-bottom: 335px;

  @include vp-1279 {
    padding-top: 40px;
    flex-direction: column;
  }

  @include vp-767 {
    margin-bottom: 315px;
  }
}

.concept h2 {
  @include font-settings ($ff-ttt, 800, 38px, 1, $color-default-white);
  text-transform: uppercase;
  letter-spacing: 0.02em;
  margin: 0 88px 0 0;

  @include vp-1279 {
    margin-bottom: 36px;
  }

  @include vp-767 {
    font-size: 18px;
    margin: 0 auto;
    margin-bottom: 36px;
    padding-top: 22px;
  }
}

.concept p {
  @include font-settings ($ff-mont, 500, 16px, 1.3, $color-default-white);
  margin: 0;
  z-index: 10;

  @include vp-767 {
    font-size: 12px;
  }
}

.concept__content {
  padding-top: 15px;
  padding-left: 7px;
}

.concept__content p {
  width: 744px;
  margin-bottom: 16px;

  @include vp-1279 {
    padding: 0;
    line-height: 1.4;
    width: 100%;
    margin-bottom: 17px;
  }
}

.concept__content b {
  @include font-settings ($ff-mont, 700, 16px, 1.3, $color-default-white);

  @include vp-767 {
    font-size: 12px;
  }
}

.concept__second-screen {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 308px;

  @include vp-767 {
    margin-bottom: 321px;
  }
}

.concept__second-screen p {
  padding-left: 105px;
  max-width: 520px;
  margin-bottom: 25px;

  @include vp-1279 {
    padding: 0;
    max-width: 100%;
    margin-bottom: 19px;
  }
}

.concept__third-screen {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 74px;
  align-items: flex-end;

  &::after {
    position: absolute;
    content: url("../img/concept-bg-3.png");
    bottom: -350px;
    left: -150px;
    z-index: -10;
  }
}

.concept__third-screen p {
  padding-right: 70px;
  max-width: 563px;
  margin-bottom: 22px;

  @include vp-1279 {
    padding: 0;
    max-width: 100%;
    margin-bottom: 19px;
  }
}

// ticket banner

.ticket-banner {
  display: none;
}

.ticket-banner.active {
  position: fixed;
  display: block;
  width: 100%;
  max-width: 1240px;
  bottom: 19px;
  right: 105px;
  background-color: #FCCD03;
  border: 1px solid #444343;
  backdrop-filter: blur(17px);
  border-radius: 140px;
  z-index: 98;

  &.is-hidden {
    display: none;
  }

  @include vp-1365 {
    width: 265px;
    height: 155px;
    border-radius: 10px;
    bottom: 135px;
    right: -110px;
    transform: translateX(-50%);
  }
}

.ticket-banner__close-btn {
  position: absolute;
  top: -20px;
  left: -8px;
  width: 48px;
  height: 48px;
  background-color: #1A1D25;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 100;

  &::after,
  &::before {
    position: absolute;
    content: "";
    top: 23px;
    left: 17px;
    width: 15px;
    height: 2px;
    background-color: #D1D2D3;
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(135deg);
  }

  &:hover {
    background-color: #3d3d3d;
  }

  @include vp-1365 {
    width: 29px;
    height: 29px;
    top: -14px;
    left: -10px;

    &::after,
    &::before {
      width: 10px;
      top: 14px;
      left: 10px;
    }
  }
}

.ticket-banner__text {
  position: relative;
  max-width: 890px;
  padding: 14px 10px 11px 58px;
  user-select: none;

  p {
    @include font-settings ($ff-ttt, 800, 24px, 1.25, $color-default-black);
    letter-spacing: 0.02em;
    text-transform: uppercase;
    margin: 0;
  }

  img {
    display: inline-block;
    margin: 0 2px;
  }

  span {
    @include font-settings ($ff-ttt, 800, 24px, 1, $color-default-black);
    text-transform: none;
    letter-spacing: 0.02em;
  }

  &::after {
    position: absolute;
    content: url('../img/giftbox.png');
    top: -41px;
    right: -193px;
  }

  @include vp-1365 {
    padding: 24px 10px 10px;
    text-align: center;

    p,
    span {
      font-size: 14px;
      line-height: 1.2;
    }

    img {
      width: 56px;
      height: 12px;
      margin: 0;
    }

    &::after {
      top: 45px;
      right: -94px;
      transform: scale(0.5);
    }
  }

}

// buy ticket button


.buy-ticket-btn {
  display: none;
}

.buy-ticket-btn.active {
  position: fixed;
  display: block;
  bottom: 30px;
  right: 115px;
  background: $color-default-black;
  border-radius: 80px;
  z-index: 99;
  user-select: none;
  transition: all 0.3s linear;
  will-change: transform;

  a {
    display: flex;
    align-items: center;
    padding: 16px 24px;
  }

  span {
    @include font-settings ($ff-mont, 400, 14px, 1.25, $color-default-white);
    margin-right: 14px;
  }

  svg {
    fill: $color-default-white;
  }

  @media (min-width: ($vp-1280)) {
    &:hover {
      background-color: #3d3d3d;
    }
  }

  @include vp-1365 {
    width: 132px;
    height: 44px;
    bottom: 150px;
    right: 90px;
    border-radius: 55px;

    span {
      @include font-settings ($ff-mont, 400, 11px, 1.25, $color-default-white);
      margin-right: 4px;
    }

    a {
      padding: 11px 14px;
    }

    svg {
      width: 22px;
      height: 22px;
    }
  }
}

.buy-ticket-btn.active.is-accent {
  background-color: $color-accent;
  color: $color-default-black;
  bottom: 20px;

  span {
    @include font-settings ($ff-mont, 400, 16px, 1.25, $color-default-black);
  }

  svg {
    color: $color-default-black;
  }

  @media (min-width: ($vp-1280)) {
    &:hover {
      background-color: #977B02;
    }
  }

  @include vp-1365 {
    width: 132px;
    height: 44px;
    bottom: 20px;
    border-radius: 55px;

    span {
      @include font-settings ($ff-mont, 400, 11px, 1.25, $color-default-black);
      margin-right: 4px;
    }
  }
}
